import ActionButton from '../common/ActionButton';
import CloseButton from '../common/CloseButton';
import AppContext from '../../AppContext';
import {ReactComponent as EnvelopeIcon} from '../../images/icons/envelope.svg';

const React = require('react');

export const MagicLinkStyles = `
    .gh-portal-icon-envelope {
        width: 44px;
        margin: 12px 0 10px;
    }

    .gh-portal-inbox-notification {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .gh-portal-inbox-notification p {
        text-align: center;
        margin-bottom: 30px;
    }
`;

export default class MagicLinkPage extends React.Component {
    static contextType = AppContext;

    renderFormHeader() {
        let popupTitle = `Bạn đã nhận được một magic link trong inbox`;
        let popupDescription = `Nếu bạn không tìm thấy, hãy kiểm tra thư mục Spam.`;

        if (this.context.lastPage === 'signup') {
            popupTitle = `Ok rồi, hãy check mail nào`;
            popupDescription = `Để hoàn thành đăng kí, hãy tìm magic link trong inbox/spam của bạn.`;
        }

        return (
            <section className="gh-portal-inbox-notification">
                <header className="gh-portal-header">
                    <EnvelopeIcon className="gh-portal-icon gh-portal-icon-envelope" />
                    <h2 className="gh-portal-main-title">{popupTitle}</h2>
                </header>
                <p>{popupDescription}</p>
            </section>
        );
    }

    renderLoginMessage() {
        return (
            <>
                <div
                    style={{
                        color: '#1d1d1d',
                        fontWeight: 'bold',
                        cursor: 'pointer'
                    }}
                    onClick={() => this.context.onAction('switchPage', {page: 'signin'})
                    }
                >
                    Quay lại đăng nhập
                </div>
            </>
        );
    }

    handleClose(e) {
        this.context.onAction('closePopup');
    }

    renderCloseButton() {
        const label = 'Đóng';
        return (
            <ActionButton
                style={{width: '100%'}}
                onClick={e => this.handleClose(e)}
                brandColor={this.context.brandColor}
                label={label}
            />
        );
    }

    render() {
        return (
            <div className="gh-portal-content">
                <CloseButton />
                {this.renderFormHeader()}
                {this.renderCloseButton()}
            </div>
        );
    }
}
